/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import legalEntityData from './data-bb-legalentity-http';

const legalEntityDataModuleKey = 'data-bb-legalentity-http-ng';

export const legalEntityDataKey = 'data-bb-legalentity-http-ng:legalEntityData';

export default ng
  .module(legalEntityDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(legalEntityDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       legalEntityData(config),
      ],
    };
  }])
  .name;



